
import Vue from 'vue'
import Vuex from 'vuex'
import User from './modules/User'
import Stock from './modules/Stock'

Vue.use(Vuex)

const debug = process.env.NODE_ENV !== 'production'

const store = new Vuex.Store({
  strict: debug,
  modules: {
    User: User,
    Stock: Stock
  }
})

export default store



