import {queryMarketIndexUrl, queryStockDetail} from '../../api/API'
export default {
  namespaced: true,
  state: {
    marketIndexs: [],
    detailData: {} // 股票的数据字典 以股票代码为键值
  },
  getters: {
    getStockByCode: (state) => (code) => {
      return state.detailData[code] || {}
    }
  },
  actions: {
    async updateMarketIndex ({commit}) {
      let sc = document.createElement('script')
      sc.onload = () => {
        document.getElementsByTagName('head')[0].removeChild(sc)
        sc = null
        let indexData = []
        if (window.hq_str_s_sh000001) {
          let t = window.hq_str_s_sh000001.split(',')
          indexData.push({stock_code: 'sh000001', stock_name: t[0], now_price: t[1], dif_money: parseFloat(t[2]).toFixed(2), dif_rate: t[3]})
        }
        if (window.hq_str_s_sz399001) {
          let t = window.hq_str_s_sz399001.split(',')
          indexData.push({stock_code: 'sz399001', stock_name: t[0], now_price: t[1], dif_money: parseFloat(t[2]).toFixed(2), dif_rate: t[3]})
        }
        if (window.hq_str_s_sz399006) {
          let t = window.hq_str_s_sz399006.split(',')
          indexData.push({stock_code: 'sz399006', stock_name: t[0], now_price: t[1], dif_money: parseFloat(t[2]).toFixed(2), dif_rate: t[3]})
        }
        commit('updateMarketIndex', indexData)
      }
      sc.src = queryMarketIndexUrl()
      document.getElementsByTagName('head')[0].appendChild(sc)
    },
    async updateStock ({commit}, stockCode, token) {
      let res = await queryStockDetail(stockCode, token, true)
      if (res.code === 200) {
        commit('updateStock', res.data)
      }
    }
  },
  mutations: {
    updateMarketIndex (state, datas) {
      state.marketIndexs = datas
    },
    updateStock (state, data) {
      state.detailData[data.stock_code] = data
    }

  }
}



