import Vue from 'vue'
import App from './App'
import router from './router'
import EventBus from './utils/EventBus'
import { Toast, Indicator, Popup } from 'mint-ui'
import store from './store'
import CacheUtil from './utils/CacheUtil'
import {createTrackNumber} from './utils/LogUtil'

Vue.component(Popup.name, Popup)
Vue.config.productionTip = false

let loadingCount = 0
let loadingTimer = null
window.EventBus = EventBus
EventBus.$on('loadingSet', (status) => {
  if (status === true) {
    loadingCount = loadingCount < 0 ? 0 : loadingCount
    loadingCount += 1
    if (loadingCount === 1) {
      if (loadingTimer !== null) {
        clearTimeout(loadingTimer)
      }
      Indicator.open()
    }
  } else {
    loadingCount -= 1
    if (loadingCount === 0) {
      loadingTimer = setTimeout(() => {
        Indicator.close()
        loadingTimer = null
      }, 500)
    }
  }
})
EventBus.$on('globalMessage', (message) => {
  Toast({
    message: message,
    position: 'bottom',
    duration: 3000
  })
})

const startApp = () => {
  /* eslint-disable no-new */
  new Vue({
    el: '#app',
    store,
    router,
    components: { App },
    template: '<App/>'
  })
}

if (CacheUtil.fetchFromCookie('dm_uuid') === null) {
  CacheUtil.saveToCookie('dm_uuid', createTrackNumber(), 3600 * 24 * 365)
}

startApp()

let setSize = () => {
  document.documentElement.style.fontSize = 20 * document.documentElement.clientWidth / 375 + 'px'
}
setSize()
window.addEventListener('resize', setSize, false)



