import CacheUtil from '../../utils/CacheUtil'
export default {
  namespaced: true,
  state: {
    uuid: null,
    token: null
  },
  actions: {},
  mutations: {
    updateToken (state, token) {
      state.token = token
      if (token === null) {
        CacheUtil.removeFromCookie(`dm_token`)
      }
    },
    updateUser (state, userInfo) {
      state.user = userInfo
    },
    updateUUID (state) {
      state.uuid = CacheUtil.fetchFromCookie(`dm_uuid`)
    }
  }
}



