import Vue from 'vue'
import CacheUtil from '../utils/CacheUtil'
import Router from 'vue-router'
Vue.use(Router)

let checkLogin = (to, from, next) => {
  if (CacheUtil.fetchFromCookie('dm_token') === null) {
    let loginRoute = {path: '/login', query: {backRoute: encodeURIComponent(to.fullPath)}}
    next(loginRoute)
  } else {
    next()
  }
}

const router = new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: resolve => require(['@/pages/Home/index'], resolve)
    },
    {
      path: '/register',
      name: 'Register',
      component: resolve => require(['@/pages/Register/index'], resolve)
    },
    {
      path: '/login',
      name: 'Login',
      component: resolve => require(['@/pages/Login/index'], resolve)
    },
    {
      path: '/resetpass',
      name: 'ResetPass',
      component: resolve => require(['@/pages/Setting/resetpass'], resolve)
    },
    {
      path: '/collection',
      name: 'Collection',
      beforeEnter: checkLogin,
      component: resolve => require(['@/pages/Collection/index'], resolve)
    },
    {
      path: '/strategy',
      name: 'Strategy',
      beforeEnter: checkLogin,
      component: resolve => require(['@/pages/Strategy/index'], resolve)
    },
    {
      path: '/strategy/create/:code',
      name: 'StrategyCreate',
      beforeEnter: checkLogin,
      component: resolve => require(['@/pages/Strategy/create'], resolve)
    },
    {
      path: '/profile',
      name: 'Profile',
      component: resolve => require(['@/pages/Profile/index'], resolve)
    },
    {
      path: '/activity',
      name: 'Activity',
      component: resolve => require(['@/pages/Activity/index'], resolve)
    },
    {
      path: '/recharge',
      name: 'Recharge',
      beforeEnter: checkLogin,
      component: resolve => require(['@/pages/Profile/recharge'], resolve)
    },
    {
      path: '/withdraw',
      name: 'Withdraw',
      beforeEnter: checkLogin,
      component: resolve => require(['@/pages/Profile/withdraw'], resolve)
    },
    {
      path: '/generalize',
      name: 'Generalize',
      beforeEnter: checkLogin,
      component: resolve => require(['@/pages/Profile/generalize'], resolve)
    },
    {
      path: '/financialdetails',
      name: 'FinancialDetails',
      beforeEnter: checkLogin,
      component: resolve => require(['@/pages/Profile/financialDetails'], resolve)
    },
    {
      path: '/deallog',
      name: 'DealLog',
      beforeEnter: checkLogin,
      component: resolve => require(['@/pages/Profile/dealLog'], resolve)
    },
    {
      path: '/coopagree',
      name: 'CoopAgree',
      component: resolve => require(['@/pages/Profile/coopAgree'], resolve)
    },
    {
      path: '/aboutus',
      name: 'AboutUs',
      component: resolve => require(['@/pages/Profile/aboutUs'], resolve)
    },
    {
      path: '/search',
      name: 'Search',
      component: resolve => require(['@/pages/Search/index'], resolve)
    },
    {
      path: '/stock/:stock_code',
      name: 'Stock',
      component: resolve => require(['@/pages/Stock/index'], resolve)
    },
    {
      path: '/setting',
      name: 'Setting',
      beforeEnter: checkLogin,
      component: resolve => require(['@/pages/Setting/index'], resolve)
    },
    {
      path: '/setnickname',
      name: 'SetNickname',
      beforeEnter: checkLogin,
      component: resolve => require(['@/pages/Setting/setNickname'], resolve)
    },
    {
      path: '/setdealpass',
      name: 'SetDealPass',
      beforeEnter: checkLogin,
      component: resolve => require(['@/pages/Setting/setDealPass'], resolve)
    },
    {
      path: '/bindphone',
      name: 'BindPhone',
      beforeEnter: checkLogin,
      component: resolve => require(['@/pages/Setting/bindPhone'], resolve)
    },
    {
      path: '/certification',
      name: 'Certification',
      beforeEnter: checkLogin,
      component: resolve => require(['@/pages/Setting/certification'], resolve)
    }
  ]
})

router.beforeEach((to, from, next) => {
  window.EventBus.$emit('PAGE_LOADING', true)
  next()
})
router.afterEach((to, from) => {
  window.EventBus.$emit('PAGE_LOADING', false)
})

export default router



