import axios from 'axios'
import qs from 'qs'
import config from './default_service.config'
import { Toast } from 'mint-ui'
import EventBus from '../utils/EventBus'

axios.defaults.baseURL = window.location.host.indexOf('localhost') > -1 ? config.baseURL_TEST : config.baseURL
axios.defaults.timeout = config.timeout
axios.defaults.headers = config.headers

axios.interceptors.request.use(config => {
  if (!config.silent) {
    EventBus.$emit('loadingSet', true)
  }
  return config
}, error => {
  EventBus.$emit('loadingSet', false)
  EventBus.$emit('globalMessage', '网络异常……')
  return error
})

axios.interceptors.response.use(response => {
  if (!response.config.silent) {
    EventBus.$emit('loadingSet', false)
    if (response.data.code !== 200 && response.data.hasOwnProperty('msg')) {
      Toast({
        message: `${response.data.msg}`,
        position: 'bottom',
        duration: 5000
      })
    }
  }
  return response.data
}, error => {
  console.error(error)
  if (!error.config.silent) {
    let message = '服务器开小差了……'
    if (error.message === 'Network Error') {
      message = '网络异常……'
    } else if (error.message.indexOf('timeout') > -1) {
      message = '请求超时……'
    }
    EventBus.$emit('loadingSet', false)
    EventBus.$emit('globalMessage', message)
  }
  return error
})

export default {
  async post (url, data, silent, serviceConfig) {
    let conf = {
      method: 'post',
      url: url
    }
    if (data) {
      if (serviceConfig && (serviceConfig.headers['Content-Type'] === 'application/json;charset=UTF-8' || serviceConfig.headers['Content-Type'] === 'multipart/form-data')) {
        conf.data = data
      } else {
        conf.data = qs.stringify(data)
      }
    }

    if (silent) {
      conf.silent = true
    }
    if (serviceConfig) {
      conf.baseURL = serviceConfig.baseURL || axios.defaults.baseURL
      conf.timeout = serviceConfig.timeout
      conf.headers = serviceConfig.headers
    }
    const res = await axios(conf)
    return res
  },

  async get (url, params, silent, serviceConfig) {
    let param = params || {}
    param.t = new Date().getTime()
    let conf = {
      method: 'get',
      url: url,
      param
    }
    if (silent) {
      conf.silent = true
    }
    if (serviceConfig) {
      conf.baseURL = serviceConfig.baseURL || axios.defaults.baseURL
      conf.timeout = serviceConfig.timeout
      conf.headers = serviceConfig.headers
    }
    const res = await axios(conf)
    return res
  }
}



