const hostSplit = window.location.host.split('.')

const config = {
  baseURL: window.__SONGSHU__ ? window.__SONGSHU__.api[window.location.host] : `http://api.${hostSplit[hostSplit.length - 2]}.${hostSplit[hostSplit.length - 1]}`, // 正式
  baseURL_TEST: 'http://api.dianmai361.com', // 测试
  timeout: 30000,
  headers: {
    'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
  }
}
export default config



