import CacheUtil from './utils/CacheUtil'
export default {
  name: 'App',
  data () {
    return {
      newsVisible: false,
      pageLoading: false
    }
  },
  components: {
    NewsContent: () => import('./components/NewsContent.vue')
  },
  beforeMount () {
    this.$store.commit('User/updateUUID')
    if (CacheUtil.fetchFromCookie(`dm_token`)) {
      this.$store.commit('User/updateToken', decodeURIComponent(CacheUtil.fetchFromCookie(`dm_token`)))
    }
    this.$store.dispatch('Stock/updateMarketIndex')
  },
  mounted () {
    window.EventBus.$on('show_news', this.showNews)
    window.EventBus.$on('close_news', this.closeNews)
    window.EventBus.$on('PAGE_LOADING', this.setPageLoading)
  },
  methods: {
    showNews (id) {
      if (window.history && window.history.pushState) {
        window.history.pushState({title: "news", url: "#"}, "news", "#")
      }
      this.newsVisible = true
      window.addEventListener("popstate", this.onPopState, false)
      window.$('body').addClass('no-scroll')
    },
    closeNews () {
      this.newsVisible = false
      window.$('body').removeClass('no-scroll')
    },
    onPopState () {
      window.removeEventListener("popstate", this.onPopState)
      this.closeNews()
    },
    setPageLoading (status) {
      this.pageLoading = status
    }
  }
}



