import service from './service-client'
import defaultConfig from './default_service.config'

let baseUrl = window.location.host.indexOf('local') > -1 ? defaultConfig.baseURL_TEST : defaultConfig.baseURL

/**
 * 发送手机验证码
 * @param mobile
 * @param uuid
 * @param token
 * @returns {*|Promise}
 */
export function sendSMSCode (mobile, uuid, token = null) {
  let p = {mobile: mobile, uuid: uuid}
  if (token) {
    p.token = token
  }
  return service.post('/v1/code/smscode', p)
}

