/**
 * 个人中心-充值-快捷方式短信确认
 * @param {string} token
 * @param {string} sms_code
 * @param {string} res_url
 * @param {object} channel_id
 */
export function requestPayBindPay (token, sms_code, res_url) {
  // return service.post('/v1_home/pay/pay', {token, sms_code})
  let urlArr = res_url.split('/')
  let baseURL = ''
  let url = ''
  for (let i = 0; i < urlArr.length; i++) {
    if (i < 3) {
      baseURL += urlArr[i] + '/'
    } else {
      url += '/' + urlArr[i]
    }
  }
  return service.post(url, {token, sms_code}, {baseURL})
}

