/**
 * 设置-认证
 * @param {string} token
 * @param {string} real_name
 * @param {string} card_id
 * @param {string} img_1
 * @param {string} img_2
 */
export function requestRealAuth (token, real_name, card_id, img_1, img_2) {
  let formData = new FormData()
  formData.append('token', token)
  formData.append('real_name', real_name)
  formData.append('card_id', card_id)
  formData.append('img_1', img_1)
  formData.append('img_2', img_2)
  return service.post('/v1_home/user/real-auth', formData, false, {headers: {'Content-Type': 'multipart/form-data'}})
}

